import React, {Component} from 'react';
import PropsTypes from 'prop-types';
import {NavLink} from 'react-router-dom';

class ResizableMenu extends Component {


static propsTypes = {
  menuOpen: PropsTypes.bool.isRequired
}

state= {
  menuOpen: false
}

componentWillReceiveProps(update){
  this.setState({menuOpen: update.menu});
}

handleMenuItemClick = (evt) => {

  console.log(evt);

  const panel = document.querySelector('.resizable-menu-container');  //finsh it 
  panel.style.opacity = '0';

  const navToggle = document.querySelector('#toggle');
  navToggle.classList.toggle('on');

  this.props.onMenuItem(evt.currentTarget.dataset.id)
}

render(){
if(this.state.menuOpen){
  return(
    <div className='resizable-menu-container'>
      <ul className= 'resizable-menu'>
        <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'menu'><NavLink to='/home'>Home</NavLink></li>
        <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'parties'><NavLink to="/about">About us</NavLink></li>
        <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'gallery'><NavLink to="#">Services</NavLink></li>
        <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'contact'><NavLink to="/donate">Donate</NavLink></li>
        <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'contact'><NavLink to="/donate">Event</NavLink></li>
        <li className= 'nav-item' onClick = {this.handleMenuItemClick} data-id = 'contact'><NavLink to="/donate">Contact</NavLink></li>
      </ul>
    </div>
    );
  }else {
    return(<div></div>);
  }

  }
}


export default ResizableMenu;
